export function detectBannedWords(prompt: string): string[] {
  const matches: string[] = [];
  bannedWords.forEach((word) => {
    const regex = new RegExp(`\\b${word}\\b`, "gi");
    const wordMatches = prompt.match(regex);
    if (wordMatches) {
      wordMatches.forEach((match) => {
        matches.push(match);
      });
    }
  });
  return matches;
}
